 class OpalWoocommerceSingleProduct {
    constructor() {
        //if ($('body').hasClass('woocommerce-single-style-5')) {
        //    $(window).load(() => {
        //        this.initStyle5();
        //    }).resize(() => {
        //        this.initStyle5();
        //    }).scroll(() => {
        //        this.initStyle5();
        //    })
        //}

    }



    initStyle5() {
        let adminbar = 30;
        if ($('body').hasClass('admin-bar')) {
            adminbar += $('#wpadminbar').outerHeight();
        }
        let $container = $('#product-2611');
        let $content   = $container.find('.entry-summary');
        let $cart      = $container.find('.woocommerce-cart');
        let $gallery   = $container.find('.woocommerce-product-gallery');

        let cartWidth    = $cart.outerWidth();
        let contentWidth = $content.outerWidth();

        let topDistance   = $(document).scrollTop();
        let productOffset = $container.offset();
        let productHeight = $container.outerHeight();

        //let productWidth  = $container.let
        let positionTop        = topDistance + adminbar - $container.offset().top;
        let positionBottom = $gallery.innerHeight() - this.getElementHeight($cart);

        if (positionTop > 0 && positionTop <= positionBottom ) {
            $gallery.addClass('push-sm-3');
            $cart.css({
                position: 'fixed',
                left    : $cart.offset().left,
                top     : adminbar,
                width   : cartWidth,

            });
            $content.css({
                position: 'fixed',
                left    : $content.offset().left,
                top     : adminbar,
                width   : contentWidth,

            });
        } else if (positionTop > 0 && positionTop > positionBottom) {
            $gallery.removeClass('push-sm-3');
            $cart.attr('style', '');
            $content.attr('style', '');
        } else {
            $gallery.removeClass('push-sm-3');
            $cart.attr('style', '');
            $content.attr('style', '');
        }
    }

    getElementHeight($element){
        let _height = 90;
        $element.find('>*').each((index, ele) => {
           _height += $(ele).outerHeight();
        });
        return _height;
    }
}

new OpalWoocommerceSingleProduct();